.first-water-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color:
    #52584D;
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.first-water-month {
  font-size: 0.75rem;
  font-weight: bold;
}

.first-water-year {
  font-size: 0.75rem;
}

.first-water-day {
  font-size: 1.5rem;
  font-weight: bold;
}